export default {
	data() {
		return {
			goodsList: [],
			totalPrice: 0,
			site: {},
			myCart: []
		}
	},
	onLoad() {
		this.getValue()

		// siteDefault
		uni.request({
			url: `http://47.104.29.236:9999/api/v1/siteDefault`, //仅为示例，并非真实接口地址。
			success: (res) => {

				if (res.data.ok == 1) {
					this.site = res.data.data[0]
				}
			}
		});

	},
	methods: {
		getValue() {
			let value = uni.getStorageSync('goodsData')

			value = JSON.parse(value)
			let cart = []
			let ids = []
			value.goodsData.forEach(item => {
				cart.push(item.cartid)
				ids.push(item.id)
			})
			this.myCart = cart
			uni.request({
				url: `http://47.104.29.236:9999/api/v1/purchase/${ids.join(",")}`, //仅为示例，并非真实接口地址。
				success: (res) => {
					if (res.data.ok === 1) {
						this.goodsList = res.data.data;
						this.goodsList.forEach((item) => {
							let i = value.goodsData.findIndex(v => {
								return item.id == v.id
							})
							item.num = value.goodsData[i][value.goodsData[i].id].number
							this.totalPrice += item.num * item.menu_price
						})
					}
				}
			});
		},
		// 创建订单
		createOrder() {
			// console.log(this.goodsList)
			let goodsdata = []
			this.goodsList.forEach(item => {
				goodsdata.push({
					id: item.id,
					menu_price: item.menu_price,
					menu_num: item.num,
					menu_name: item.menu_name
				})
			})
			// createorder_st
			let data = {
				...this.site,
				totalPrice: this.totalPrice,
				goodsdata: goodsdata,
				cartid: this.myCart
			}
			uni.request({
				method: 'POST',
				url: `http://47.104.29.236:9999/api/v1/createorder_st`, //仅为示例，并非真实接口地址。
				data: {
					data: data
				},
				success: (res) => {
					let pay = {
						id:res.data.id,
						path:'pages/meituan/order'
					}
					pay = JSON.stringify(pay)
					uni.setStorageSync('pay', pay);
					if (res.data.ok == 1) {
						let CurrentPages = getCurrentPages()
						uni.redirectTo({
							url: `/pages/meituan/payment?id=${res.data.id}&path=pages/meituan/order`
						});
					}
				}
			});
		},
		// 去地址页
		toseit() {
			let CurrentPages = getCurrentPages()
			uni.reLaunch({
				url: `/pages/xy/xy-site/site?path=${CurrentPages[CurrentPages.length-1].route}`
			});
		}
	}
}
